.Poster{
    width: 100%;
    height: 70vh;
    padding: 0px;
    margin-bottom: -35px;
}
.Poster--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0) 75%, rgba(255,255,255,1) 100%);
}

@media (max-width: 600px) {
    .Poster{
        height: 60vh;
    }
}