.Body{
    width: 100%;
    margin: 5px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
}
.Body--Design{
    max-width: 800px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 0px;
}
h1,p{
    margin: 0px 10px;
}
h1{    font-weight: bold;   
}
.Body--Design h3{
    font-weight: bold;
    color: #fff;
}
.Body--Design button{
    background-color: #141414c2;
    margin: 10px;
    padding: 5px 30px;
    border: none;
    border-radius: 2px;
}
.Card--Demais{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 30px;
    padding: 0px;
}
.Card{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;    
    padding: 5px;
    margin: 5px;
    border: #bbb solid 2px;    
}
.Card h3{
    font-weight: bold;
    margin: 0px;
}
.Card img{
    width: 100%;
    height: 200px;
    object-fit: cover;
    border: none;
    border-radius: 2px;
    transform: scale(.9);    
}
.Card p{
    width: 100%;
    font-size:12pt;  
}
@media (min-width: 0px) {
    .Card{
        width: 95%;
    }
}
@media (min-width: 480px) {
    .Card{
        width: 44%;
    }
}
@media (min-width: 650px) {
    .Card{
        width: 300px;
    }
}