.Header{
    position: fixed;
    top:0px;
    left: 0px;
    height: 50px;
    width: 100%;
    margin: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    z-index: 9;
}
.Header--logo{
    height: 40px;
    width: 90px;
    margin: 5px;
    cursor: pointer
}
.Header--logo img{
    width: 40px;
    height: 100%;
}
.Header--menu{
    height: 50px;
    margin: 0px 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    
}
.Header--menu button{
    background-color: #141414dc;
    margin: 0px 5px;
    padding: 0px 5px;
    width: 70px;
    height: 30px;
    border: none;
    border-radius: 2px;
    color: white;
    font-weight: bold;
    cursor: pointer
}
.Header--contact{
    height: 35px;
    width: 90px;
    margin: 5px;
    display: inline-flex;
    cursor: pointer
}
.Header--contact a {
    margin: 0px 5px;
    
}
.Header--contact img{
    width: 100%;
    height: 100%;    
}
