@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{
  padding: 0px;
  margin: 0px;  
}

body {
  width: 100%;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Poster{
    width: 100%;
    height: 70vh;
    padding: 0px;
    margin-bottom: -35px;
}
.Poster--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(255,255,255,0) 75%, rgba(255,255,255,1) 100%);
}

@media (max-width: 600px) {
    .Poster{
        height: 60vh;
    }
}
.Header{
    position: fixed;
    top:0px;
    left: 0px;
    height: 50px;
    width: 100%;
    margin: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    z-index: 9;
}
.Header--logo{
    height: 40px;
    width: 90px;
    margin: 5px;
    cursor: pointer
}
.Header--logo img{
    width: 40px;
    height: 100%;
}
.Header--menu{
    height: 50px;
    margin: 0px 5px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    
}
.Header--menu button{
    background-color: #141414dc;
    margin: 0px 5px;
    padding: 0px 5px;
    width: 70px;
    height: 30px;
    border: none;
    border-radius: 2px;
    color: white;
    font-weight: bold;
    cursor: pointer
}
.Header--contact{
    height: 35px;
    width: 90px;
    margin: 5px;
    display: inline-flex;
    cursor: pointer
}
.Header--contact a {
    margin: 0px 5px;
    
}
.Header--contact img{
    width: 100%;
    height: 100%;    
}

.Body{
    width: 100%;
    margin: 5px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
}
.Body--Design{
    max-width: 800px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    margin: 0px;
}
h1,p{
    margin: 0px 10px;
}
h1{    font-weight: bold;   
}
.Body--Design h3{
    font-weight: bold;
    color: #fff;
}
.Body--Design button{
    background-color: #141414c2;
    margin: 10px;
    padding: 5px 30px;
    border: none;
    border-radius: 2px;
}
.Card--Demais{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 30px;
    padding: 0px;
}
.Card{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;    
    padding: 5px;
    margin: 5px;
    border: #bbb solid 2px;    
}
.Card h3{
    font-weight: bold;
    margin: 0px;
}
.Card img{
    width: 100%;
    height: 200px;
    object-fit: cover;
    border: none;
    border-radius: 2px;
    -webkit-transform: scale(.9);
            transform: scale(.9);    
}
.Card p{
    width: 100%;
    font-size:12pt;  
}
@media (min-width: 0px) {
    .Card{
        width: 95%;
    }
}
@media (min-width: 480px) {
    .Card{
        width: 44%;
    }
}
@media (min-width: 650px) {
    .Card{
        width: 300px;
    }
}
.App {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
}

.Projetos{
    width: 100%;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Projetos--Videos,
.Projetos--Imgs
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 50%;
    border-radius: 2px; 
}
.Projetos--Videos iframe,
.Projetos--360 iframe,
.Projetos--Imgs img
{    
    height: 90vh;
    width: calc(100% - 4px);
    padding:1px
}
.Projetos--seta--left,.Projetos--seta--right{
    position:absolute;
    width:40px;
    background-color: rgba(255,255, 255, .6);
   -webkit-filter:invert(1);
           filter:invert(1);
    border-radius: 100%;
    z-index: 99;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;    
    padding: 0px 5px;
    overflow:hidden;
    opacity: 1;
    transition: 0.5s;
    -webkit-transform: scale(.9);
            transform: scale(.9);
}

.Projetos--360{
    width: 100%;
    height: 100%;
    border-radius: 2px;  
}

.Projetos--Imgs .Projetos--seta--left{
    left:15px;
}
.Projetos--Imgs .Projetos--seta--right{
    right:50%;
}
.Projetos--Videos .Projetos--seta--left{
    left:50%
}
.Projetos--Videos .Projetos--seta--right{
    right:15px;
}
@media (max-width: 600px) {
    .Projetos--Videos{width: 100%;   } 
    .Projetos--Videos iframe,
    .Projetos--Imgs img,
    .Projetos--360 iframe
    {    
        height: 60vh;
    }   
    .Projetos--Imgs{width: 100%;}    
    .Projetos--Imgs .Projetos--seta--left{      left:10px;  }
    .Projetos--Imgs .Projetos--seta--right{     right:10px; }
    .Projetos--Videos .Projetos--seta--left{    left:15px;  }
    .Projetos--Videos .Projetos--seta--right{   right:15px; }    
}
.Footer{
    height: 50px;
    width: 100%;
    background-color: #ddd;
    margin-top: -4px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    flex-wrap: wrap;
    color: rgb(54, 54, 54);
}
