.Projetos{
    width: 100%;
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
}
.Projetos--Videos,
.Projetos--Imgs
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    width: 50%;
    border-radius: 2px; 
}
.Projetos--Videos iframe,
.Projetos--360 iframe,
.Projetos--Imgs img
{    
    height: 90vh;
    width: calc(100% - 4px);
    padding:1px
}
.Projetos--seta--left,.Projetos--seta--right{
    position:absolute;
    width:40px;
    background-color: rgba(255,255, 255, .6);
   filter:invert(1);
    border-radius: 100%;
    z-index: 99;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;    
    padding: 0px 5px;
    overflow:hidden;
    opacity: 1;
    transition: 0.5s;
    transform: scale(.9);
}

.Projetos--360{
    width: 100%;
    height: 100%;
    border-radius: 2px;  
}

.Projetos--Imgs .Projetos--seta--left{
    left:15px;
}
.Projetos--Imgs .Projetos--seta--right{
    right:50%;
}
.Projetos--Videos .Projetos--seta--left{
    left:50%
}
.Projetos--Videos .Projetos--seta--right{
    right:15px;
}
@media (max-width: 600px) {
    .Projetos--Videos{width: 100%;   } 
    .Projetos--Videos iframe,
    .Projetos--Imgs img,
    .Projetos--360 iframe
    {    
        height: 60vh;
    }   
    .Projetos--Imgs{width: 100%;}    
    .Projetos--Imgs .Projetos--seta--left{      left:10px;  }
    .Projetos--Imgs .Projetos--seta--right{     right:10px; }
    .Projetos--Videos .Projetos--seta--left{    left:15px;  }
    .Projetos--Videos .Projetos--seta--right{   right:15px; }    
}